<template>
  <div class="ui-wrap">
    <AppHeader title="收货地址管理" showBack="true"></AppHeader>
    <div class="addr-wrap">
      <div class="ui-title">
        <a href="javascript:void (0)" style="color: #333;" @click="addAddress">添加收货地址</a>
      </div>
      <address-item
        v-for="(item, index) in addressList"
        :key="index"
        :address="item"
      ></address-item>
    </div>
    <mt-popup
      style="height: 100%; width: 100%; z-index: 99999;"
      v-model="addAddressViewState">
      <address-add></address-add>
    </mt-popup>
  </div>
</template>

<script>
import AddressItem from './address-item.vue'
import AppHeader from '../../../components/header/header.vue'
import AddressAdd from './address-add.vue'
import bus from '../../../assets/js/bus'
import {getAddressList, removeAddress, setDefaultAddress} from '../../../api/home'
import { Toast } from 'mint-ui'
export default {
  name: 'Address',
  data () {
    return {
      addAddressViewState: false,
      addressList: []
    }
  },
  created () {
    this.getDatas()
  },
  mounted () {
    this.$on('removeOneAddress', address => {
      removeAddress(address.address_id)
        .then(data => {
          if (data.state) {
            Toast('删除成功')
            this.getDatas()
          } else {
            Toast(data.errormsg || '删除失败')
          }
        })
    })
    this.$on('setOneDefaultAddress', address => {
      setDefaultAddress(address.address_id)
        .then(data => {
          if (data.state) {
            Toast('设置成功')
            this.getDatas()
          } else {
            Toast(data.errormsg || '设置失败')
          }
        })
    })
    bus.$on('addressSuccess', () => {
      this.getDatas()
      this.$set(this, 'addAddressViewState', false)
    })
    bus.$on('clickCloseModal', () => {
      this.addAddressViewState = false
    })
  },
  methods: {
    getDatas () {
      getAddressList()
        .then(data => {
          if (data.state) {
            this.$set(this, 'addressList', data.data.list)
          } else {
            Toast(data.errormsg)
          }
        }).catch(err => console.log(err))
    },
    addAddress () {
      this.$set(this, 'addAddressViewState', true)
    }
  },
  components: {
    AddressItem,
    AppHeader,
    AddressAdd
  }
}
</script>

<style lang="less" scoped>
  .addr-wrap{}
</style>



