<template>
  <div class="address-item">
    <div class="clearfix addr-name">
      <h3>{{address.address_name}}</h3>
      <span class="tel">{{address.address_tel}}</span>
      <mt-badge size="small" color="#888" v-show="address.default">默认</mt-badge>
    </div>
    <div class="addr-detail">
      <p>{{address.addressfull}}</p>
    </div>
    <div class="btns">
      <div class="clearfix btn">
        <a href="javascript:void(0)" @touchstart="removeAddress">删除</a>
        <a href="javascript:void(0)" v-show="!address.default" @click="setDefault">设为默认</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AddressItem',
  props: {
    address: Object
  },
  methods: {
    removeAddress () {
      this.$parent.$emit('removeOneAddress', this.address)
    },
    setDefault () {
      this.$parent.$emit('setOneDefaultAddress', this.address)
    }
  }
}
</script>

<style lang="less" scoped>
  .address-item{
    padding: .133rem .32rem;
    background-color: #fff;
    margin-top: .6rem;
    &:first-child{
      margin-top: 0;
    }
    .addr-name{
      margin: .2rem 0;
      h3{
        float: left;
        margin: 0;
        font-size: 15px;
      }
      span{
        margin: 0;
        margin-left: 2rem;
        float: left;
      }
    }
    .addr-detail{
      font-size: 12px;
      color: #999;
      margin-bottom: 1rem;
      p{
        margin: 0;
      }
    }
    .btns{
      border-top: 1px solid #eee;
      .btn{
        padding: .4rem 0;
        a{
          float: right;
          padding: 0 1.267rem;
          margin: .133rem 0 .133rem .133rem;
          height: 1.693rem;
          line-height: 1.693rem;
          text-align: center;
          border: 1px solid #999;
          border-radius: .053rem;
          font-size: 12px;
          color: #444;
        }
      }
    }
  }
</style>



